    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default { 
		data() {
			return {
				datalist:[],
				loadingText: '正在加载...',
				hasmore:true,
				curpage:1,
				predepoit:'',
				tg_predepoit:'',
				tgtoday:0,
				percent:0
			}				
		},
        computed: mapState(['hasLogin','userInfo']),  
		onLoad() {
			console.log('页面加载');
			this.$Request.post('member_index/my_asset',{fields:'predepoit'}).then(res => {
			 	if (res.code == 200) {
			 		console.log(res);
					this.predepoit = res.datas.predepoit;
			 		this.tg_predepoit = res.datas.tg_predepoit;
			 	}
			 });
			 // 获取今日收入
			 this.$Request.post('member_fund/tgtoday').then(res => {
			  	if (res.code == 200) {
			  		console.log(res);
			  		this.tgtoday = res.datas.amount;
					this.percent = 0;
					console.log(this.percent)
			  	}
			  });
			this.loadData();
		},
         onReachBottom() {
             console.log('正在加载中...');
			 this.curpage++;
			 this.loadData();
         },
         onPageScroll(){
             console.log('页面滚动...');
         },
         onPullDownRefresh() {
             console.log('上拉刷新...');
             uni.stopPullDownRefresh();
         },
        methods: {   
            async loadData() {  
				var _this = this;
				console.log('===userInfo==='+this.hasLogin);
				console.log(this.userInfo);
				console.log(this.userInfo.token);
                if (!this.hasLogin) {  
					uni.navigateTo({
						url:'login',
					})
                }else{
					if(!this.hasmore){
						this.loadingText = '暂无更多信息';
						return false;
					}
					_this.$Request.post('member_fund/predepositlog',{curpage:_this.curpage}).then(res => {
						if (res.code == 200) {
							console.log(res);
							_this.hasmore =res.hasmore;
							if(!res.hasmore){
								_this.loadingText = '暂无更多信息';
							}
							let _datalist = res.datas.list;
							for(var i=0;i<_datalist.length;i++){
								_this.datalist.push(_datalist[i]);
							}
						}
						else{
							uni.showToast({
								icon: 'none',
								title:''+res.datas.error,
								duration:2000
							});
							uni.navigateTo({
								url:'login',
							});
						}
					});
				}
            },
			toPage: function(url) {
				uni.navigateTo({
					url:url,
				})
			},
			toBack: function() {
				uni.switchTab({
					url:'/pages/member/home',
				})
			}
		},
    }  
